<template>
  <div class="professional padding students">
    <!-- 专业信息 -->

    <!-- 弹框组件 -->
    <el-dialog
      :title="Addpro"
      :visible.sync="dialogFormVisible"
      :before-close="handleClose"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="form">
        <el-form-item label="所属学院：" :label-width="formLabelWidth" prop="academyId">
          <el-select v-model="form.academyId" placeholder="请选择所属学院" @change="searchacademyId">
            <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="专业代码：" :label-width="formLabelWidth" prop="code">
          <el-input v-model="form.code" auto-complete="off" placeholder="请输入专业代码"></el-input>
        </el-form-item>
        <el-form-item label="专业名称：" :label-width="formLabelWidth" prop="name">
          <el-input v-model="form.name" auto-complete="off" placeholder="请输入专业名称"></el-input>
        </el-form-item>
        <el-form-item label="专业负责人：" :label-width="formLabelWidth" prop="creatorVal">
          <el-select v-model="form.creatorVal" placeholder="请选择专业负责人" filterable>
            <el-option
              v-for="item in creatorValArr"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-row>
          <el-col :span="10">
            <el-form-item label="修业年限：" :label-width="formLabelWidth" prop="lengthOfSchooling">
              <el-input v-model="form.lengthOfSchooling" auto-complete="off" placeholder="请输入修业年限"></el-input>
              <p style="position: absolute;top: 0;right: -23px;">年</p>
            </el-form-item>
          </el-col>
        </el-row>
        <el-form-item label="是否省级一流专业：" :label-width="formLabelWidth" prop="isfirst">
          <el-select style="width:200px" v-model="form.isfirst" placeholder="请选择">
            <el-option
              label="是"
              :value="0"
            ></el-option>
            <el-option
              label="否"
              :value="1"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="认证状态：" :label-width="formLabelWidth" prop="authenticationStatus">
          <el-select style="width:200px" v-model="form.authenticationStatus" placeholder="请选择认证状态">
            <el-option
              v-for="item in statusOptions"
              :key="item.code"
              :label="item.value"
              :value="item.code"
            ></el-option>
          </el-select>
        </el-form-item>

        <el-form-item label="专业简介：" :label-width="formLabelWidth">
          <el-input v-model="form.intro" auto-complete="off" type="textarea" :rows="7" placeholder="请输入专业简介"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="ppp">取 消</el-button>
        <el-button type="primary" @click="seccons1('form')" v-show="falg">确 定</el-button>
        <el-button type="primary" @click="seccons2('form')" v-show="falg1">确 定</el-button>
      </div>
    </el-dialog>
    <crumb style="padding: 0 5px;">
      <div class="new--btn" slot="right" @click="close">+ 新建</div>
    </crumb>
    <div class="professional-tab1">
      <div class="professional-inp">
        <el-form :inline="true" :model="formInline" class="demo-form-inline">
          <span class="ssssss">所属学院：</span>
          <el-select v-model="value0" placeholder="全部" class="pppp" @change="changes1">
            <el-option v-for="item in options0" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
          <span class="ssssss">专业搜索：</span>
          <el-input class="pppp" v-model="formInline.user" placeholder="专业代码，名称"></el-input>
          <div class="search--btn" style="margin-top: 6px" @click="initData">搜索</div>
        </el-form>
      </div>
      <template>
        <el-table :data="tableData" stripe style="width: 100%" v-loading="loading">
          <el-table-column label="专业代码" width="100">
            <template slot-scope="scope" >
              <span>{{ scope.row.code }}</span>
            </template>
          </el-table-column>
          <el-table-column label="专业名称" width="120">
            <template slot-scope="scope">
              <span>{{ scope.row.name }}</span>
            </template>
          </el-table-column>
          <el-table-column label="所属学院" width="120">
            <template slot-scope="scope">
              <span>{{ scope.row.academyName }}</span>
            </template>
          </el-table-column>
          <el-table-column label="专业介绍" width="auto">
            <template slot-scope="scope">
              <el-popover
                placement="top-start"
                width="600"
                :content="scope.row.intro"
              ><div slot="reference">{{ scope.row.intro }}</div></el-popover>
            </template>
          </el-table-column>
          <el-table-column label="专业负责人" align="center" width="100">
            <template slot-scope="scope">
              <span>{{ scope.row.leaderVal }}</span>
            </template>
          </el-table-column>
          <el-table-column label="修业年限" align="center" width="100">
            <template slot-scope="scope">
              <span>{{ scope.row.lengthOfSchooling }}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="160">
           <template slot-scope="scope">
              <el-button type="text" size="small" @click="handleEdit(scope.$index, scope.row)">
                <SvgIcon icon-class="iconbianji1"></SvgIcon>&nbsp;&nbsp;编辑
              </el-button>
              <el-button style="color: #ff5858;" type="text" size="small" @click="handleDelete(scope.$index, scope.row)">
                <SvgIcon icon-class="iconshanchu"></SvgIcon>&nbsp;&nbsp;删除
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </template>
    </div>
    <div>
      <template>
        <div class="block">
          <el-pagination
            background
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="pageNum"
            :page-sizes="[15, 30, 40, 50]"
            :page-size="pageSize"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import crumb from '@/components/Crumb'
export default {
  components: {
    crumb
  },
  data() {
    let gradeYear = (rule, value, callback) => {
      let reg = /^[0-9]*$/;
      if (!reg.test(value)) {
        callback(new Error("修业年限为数字类型"));
      } else {
        callback();
      }
    };
    return {
      dialogFormVisible: false,
      formLabelWidth: "150px",
      falg: false,
      falg1: false,
      Addpro: "",
      form: {
        name: "",
        code: "",
        intro: "",
        academyId: "",
        creatorVal: "",
        lengthOfSchooling: null,
        isfirst: 0,
        authenticationStatus: 0
      },
      creatorValArr: [],
      currentPage4: 1,
      total: 0,
      page: 1,
      pageSize: 15,
      pageNum: 1,
      formInline: {
        user: ""
      },
      rules: {
        value: [
          {
            required: true,
            message: "请输入所属学院",
            trigger: "blur"
          }
        ],
        name: [
          {
            required: true,
            message: "请输入专业名称",
            trigger: "blur"
          }
        ],
        lengthOfSchooling: [
          {
            required: true,
            validator: gradeYear,
            trigger: "blur",
            message: "修业年限为数字类型"
          }
        ],
        code: [
          {
            required: true,
            message: "请输入专业编号",
            trigger: "blur"
          }
        ],
       /* creatorVal: [
          {
            required: true,
            message: "请输入专业负责人",
            trigger: "blur"
          }
        ]*/
      },
      tableData: [],
      loading: true,
      options: [],
      value: "",
      options0: [],
      value0: "",
      options1: [],
      value1: "",
      arrleaderdata: false,
      statusOptions: [
        {
            "code": 0,
            "value": "已认证"
        },
        {
            "code": 1,
            "value": "未认证"
        },
        {
            "code": 2,
            "value": "已申请"
        }
    ]
    };
  },
  mounted() {
    this.initData();
    this.allxue();
    /*this.$get('/system/dict/field/authenticationStatus').then(res => {
      this.statusOptions = res.data
    })*/
  },
  methods: {
    initData() {
      let keywords = {
        academyId: this.value0,
        keywords: this.formInline.user,
        page: this.pageNum,
        rows: this.pageSize
      };
      this.$post("/basic/specialty/list", keywords).then(res => {
        if (res.status == 1) {
          this.tableData = res.data;
          this.total = res.total;
          this.loading = false;
        }
      });
    },
    ppp(form) {
      this.$refs["form"].resetFields();
      this.dialogFormVisible = false;
      this.initData();
      this.form = {};
    },
    // /basic/academy/all 所有学院
    allxue() {
      this.$get(`/basic/academy/all?type=${1}`).then(res => {
        this.options = JSON.parse(JSON.stringify(res.data));
        this.options0 = res.data;
        this.options0.unshift({
          id: "",
          name: "全部"
        });
      });
    },
    //  专业 负责人  system/sysUserRole/getSpecialByAcademyId
    searchacademyId(arrleader) {
      let academyId = this.form.academyId.toString();
      this.$get(`basic/user/type/${1}/${academyId}`).then(res => {
        this.creatorValArr = res.data;
        if (this.arrleaderdata) {
          this.creatorValArr.push(arrleader);
        }
      });
    },
    //  跟据院校查专业
    changes1() {
      this.$get(`/basic/specialty/all/${this.value0}`).then(res => {
        this.options1 = res.data;
      });
    },
    //  新增专业     /basic/specialty/add
    specialtyadd() {
      let form = {
        name: this.form.name,
        code: this.form.code,
        intro: this.form.intro,
        academyId: this.form.academyId,
        leader: this.form.creatorVal,
        lengthOfSchooling: Number(this.form.lengthOfSchooling),
        isfirst: this.form.isfirst,
        authenticationStatus: this.form.authenticationStatus
      };
      this.$post("/basic/specialty/add", form).then(res => {
        if (res.status == 1) {
          this.$notify({
            message: "新增成功",
            type: "success",
            title: 'success'
          });
          this.initData();
        } else if (res.status == 0) {
          this.$notify.warning({
            message: res.message,
            type: "warning"
          });
        }
      });
    },
    handleEdit(index, row) {
      //编辑对话框显示值
      this.Addpro = "编辑专业";
      this.falg = false;
      this.falg1 = true;
      this.dialogFormVisible = true;
      this.form = JSON.parse(JSON.stringify(row));
      this.form.academyId = row.academyId;
      this.form.creatorVal = row.leader;
      this.form.lengthOfSchooling = row.lengthOfSchooling;
      let id = this.form.id;
      let arrleader = {
        id: row.leader,
        name: row.leaderVal
      };
      this.arrleaderdata = true;
      this.allxue();
      this.searchacademyId(arrleader);
    },
    seccons1(form) {
      // 确定按钮的点击提交编辑内容
      this.$refs[form].validate(valid => {
        if (valid) {
          this.specialtyadd();
          this.dialogFormVisible = false;
          this.form = {};
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    seccons2(form) {
      // 确定按钮的点击提交编辑内容 /basic/specialty/edit
      this.$refs[form].validate(valid => {
        if (valid) {
          let form = {
            code: this.form.code,
            name: this.form.name,
            id: this.form.id,
            intro: this.form.intro,
            academyId: this.form.academyId || "",
            lengthOfSchooling: Number(this.form.lengthOfSchooling),
            leader: this.form.creatorVal == null ? "" : this.form.creatorVal,
            isfirst: this.form.isfirst,
            authenticationStatus: this.form.authenticationStatus
          };
          this.$put("/basic/specialty/edit", form).then(res => {
            if (res.status == 1) {
              this.$notify.success({
                title: "success",
                message: "编辑成功"
              });
              this.initData();
              this.dialogFormVisible = false;
              this.$refs["form"].resetFields();
            } else if (res.status == 0) {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
              this.initData();
              this.dialogFormVisible = true;
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    close() {
      this.Addpro = "添加专业";
      this.falg = true;
      this.falg1 = false;
      this.dialogFormVisible = true;
      this.form = {
        name: "",
        code: "",
        intro: "",
        academyId: "",
        creatorVal: "",
        lengthOfSchooling: null,
        isfirst: 0,
        authenticationStatus: 0
      };
      this.allxue();
    },
    handleClose() {
      this.$refs["form"].resetFields();
      this.dialogFormVisible = false;
      this.initData();
      this.form = {};
    },
    handleDelete(index, row) {
      this.$confirm("确定删除该信息?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$deletes(`/basic/specialty/del/${row.id}`).then(res => {
            console.log(res)
            if (res.status == 1) {
              this.initData();
            } else if (res.status == 0) {
              this.$notify.warning({
                title: "warning",
                message: res.message
              });
            }
          });
        })
        .catch(() => {
          this.$notify({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    indexMethod(index) {
      return index * 1;
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.initData();
    },
    handleCurrentChange(val) {
      this.pageNum = val;
      this.initData();
    }
  }
};
</script>

<style lang="scss">
.professional {
  margin-bottom: 40px;
  .professional-inp {
    width: 100%;
    background-color: #fff;
    margin-bottom: 20px;
    padding: 20px 20px 25px;
    border-radius: 10px;
    box-sizing: border-box;
    .pppp {
      width: 12.5%;
      float: left;
      margin: 0;
      margin-right: 20px;
      margin-top: 7px;
    }

    .el-form-item {
      margin-left: 20px;
    }

    .professional-btn {
      margin-top: 6px;
      float: right;
    }
  }

  .el-select {
    display: -webkit-box;
  }

  .ssss {
    float: left;
    margin-left: 38px;
    font-size: 14px;
    line-height: 45px;
    font-weight: bold;
    color: #696969;
  }

  .ssssss {
    float: left;
    line-height: 40px;
    margin-top: 7px;
    font-weight:bold;
    color: #696969;
    font-size: 14px;
    margin-right: 11px;
  }

  .block {
    // float: left;
    width: 100%;
    text-align: right;
    padding: 20px;
    background-color: #fff;
    border-radius: 0 0 10px 10px;
    box-sizing: border-box;
    margin-bottom: 100px;
  }
}
</style>



